








































import {Component, Mixins} from "vue-property-decorator";
import att_mixins from "@/mixins/basic";
import search from "@/components/search/search.vue";
import module_init_args from "@/store/modules/init_args";
@Component({components: {search}})
export default class record extends Mixins(att_mixins) {
  columns = [
    {
      title: '工单',
      key: 'work_id'
    },
    {
      title: '工单说明',
      key: 'text',
      tooltip: true
    },
    {
      title: '执行时间',
      key: 'execute_time',
      sortType: 'desc'
    },
    {
      title: '申请人',
      key: 'username'
    },
    {
      title: '真实姓名',
      key: 'real_name'
    },
    {
      title: '当前操作人',
      key: 'assigned'
    },
    {
      title: '环境',
      key: 'idc'
    },
    {
      title: '连接名称',
      key: 'source'
    },
    {
      title: '库名',
      key: 'data_base'
    },
    {
      title: '操作',
      key: 'action',
      align: 'center',
      slot: 'action'
    }
  ];

  url = `${this.$config.url}/audit/record`

  currentPage(vl = 1) {
    this.fetch_page(vl,this.url)
  }

  openOrder(row: any) {
    module_init_args.fetch_order_item(row)
    this.$router.push({
      name: 'profile',
    })
  }

  mounted() {
    this.currentPage()
  }
}
